import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        user: null,
        profile: null
    },
    getters: {
        displayName: state => {
            if (!state.user || !state.user.email)
                return null;
            if (state.profile && state.profile.first_name)
                return state.profile.first_name;
            let displayName = state.user.email;
            let n = displayName.indexOf('@');
            displayName = displayName.substring(0, n != -1 ? n : displayName.length);
            return displayName;
        },
    },
    mutations: {
        set_user(state, user) {
            console.log('set_user', user);
            state.user = user;
        },
        set_profile(state, profile) {
            console.log('set_profile', profile);
            state.profile = profile;
        },
    },
    actions: {}
});