<template>
  <div id="app">
    <navComponent />
    <div class="container p-4" v-if="errorMessage || isLoading">
      <div class="text-center text-warning" v-if="errorMessage">
        {{ errorMessage }}
        <hr />
        <button class="btn btn-block btn-warning" @click="tray_again" type="button">Réessayer</button>
      </div>
      <loading-compoenent v-else-if="isLoading" />
    </div>
    <router-view v-else></router-view>
    <footerComponent />
  </div>
</template>

<script>
import { db } from "./firebase";

import navComponent from "./components/navComponent";
import footerComponent from "./components/footerComponent";

export default {
  name: "App",
  components: { navComponent, footerComponent },
  data() {
    return {
      profile: null,
      exists: false,
      errorMessage: null,
      isLoading: false
    };
  },
  computed: {
    currentUser() {
      return this.$store.state.user;
    }
  },
  watch: {
    currentUser(newVal) {
      this.get_profile(newVal);
    }
  },
  created() {
    console.log("App created!!!!");
  },
  methods: {
    tray_again() {
      this.isLoading = false;
      this.errorMessage = null;
      this.get_profile(this.$store.state.user);
    },
    async get_profile(user) {
      console.log("Getting the profile");
      if (user) {
        console.log("user.uid", user.uid);
        this.isLoading = true;
        this.errorMessage = null;
        db.collection("users")
          .doc(user.uid)
          .get()
          .then(profile => {
            this.isLoading = false;
            this.errorMessage = null;
            this.exists = profile.exists;
            if (this.exists) {
              console.log("Yes, we have a profile!");
              this.$store.commit("set_profile", profile.data());
              this.$router.push({
                name: "home"
              });
            } else {
              console.log("No profile ....Ooaps");
              this.$store.commit("set_profile", null);
              this.$router.push({
                name: "edit-profile"
              });
            }
          })
          .catch(error => {
            this.isLoading = false;
            this.errorMessage = error;
            console.log(error);
          });
      } else {
        console.log("No user is signed in ...");
        this.$store.commit("set_profile", null);
        this.$router.push({
          name: "home"
        });
      }
    }
  }
};
</script>

<style>
#app {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
}
</style>
