<template>
  <div class="container p-4">
    <div class="card card-body text-center text-warning" v-if="errorMessage">{{ errorMessage}}</div>
    <loading-compoenent v-else-if="isLoading" />
    <div v-else v-html="rawHtml"></div>
  </div>
</template>

<script>
export default {
  name: "pageViewerComponent",
  components: {},
  data() {
    return {
      file: null,
      rawHtml: null,
      errorMessage: null,
      isLoading: false
    };
  },
  props: {
    initialFile: String
  },
  created() {
    if (this.initialFile) this.file = this.initialFile;
    else this.file = this.$route.params.file;
    this.load_page(this.file);
  },
  watch: {
    $route(to) {
      if (to && to.params && to.params.file) this.load_page(to.params.file);
    }
  },
  methods: {
    load_page(file) {
      this.isLoading = true;
      this.errorMessage = null;
      this.$http
        .get("/pages/" + file)
        .then(response => {
          this.isLoading = false;
          this.errorMessage = false;
          //console.log(response);
          this.rawHtml = response.data;
        })
        .catch(error => {
          this.isLoading = false;
          this.errorMessage = error;
          console.log(error);
        });
    }
  }
};
</script>

