import Vue from 'vue'
import App from './App.vue'

import './assets/main.css'
import './assets/main.js'

Vue.config.productionTip = false

import router from './router'
import store from './store'
import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';
Vue.use(VueSweetalert2)

import moment from "moment";
Vue.filter('moment', function(value) {
    if (!value) return "";
    return moment(value.toDate()).format("YYYY-MM-DD hh:mm");
});

import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap-icons/font/bootstrap-icons.css'
import 'bootstrap/dist/js/bootstrap.min.js'

import { v4 as uuidv4 } from 'uuid';

Vue.mixin({
    methods: {
        uuidv4: () => uuidv4()
    }
});

const axios = require('axios');
Vue.prototype.$http = axios;

import { auth } from "./firebase";

import selectComponent from './components/helpers/selectComponent.vue'
Vue.component('select-component', selectComponent)
import loadingComponent from './components/helpers/loadingComponent.vue'
Vue.component('loading-compoenent', loadingComponent)
import pageViewerComponent from './components/pageViewerComponent.vue'
Vue.component('page-viewer-component', pageViewerComponent)

