import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'home',
    component: () =>
        import ('../components/homeComponent')
}, {
    path: '/index.html',
    name: 'home-index',
    component: () =>
        import ('../components/homeComponent')
}, {
    path: '/page-viewer/:file',
    name: 'page-viewer',
    component: () =>
        import ('../components/pageViewerComponent')
}, {
    path: '/sign-in',
    name: 'sign-in',
    component: () =>
        import ('../components/signInComponent')
}, {
    path: '/sign-out',
    name: 'sign-out',
    component: () =>
        import ('../components/signOutComponent')
}, {
    path: '/sign-up',
    name: 'sign-up',
    component: () =>
        import ('../components/signUpComponent')
}, {
    path: '/edit-profile',
    name: 'edit-profile',
    component: () =>
        import ('../components/editProfileComponent')
}, {
    path: '/inscription-distributeur',
    name: 'inscription-distributeur',
    component: () =>
        import ('../components/inscriptionDistributeurComponent')
}, {
    path: '/inscription-transporteur',
    name: 'inscription-transporteur',
    component: () =>
        import ('../components/inscriptionTransporteurComponent')
}, ]

const router = new VueRouter({
    //mode: 'history',
    base: process.env.BASE_URL,
    routes,
    scrollBehavior(to /*, from, savedPosition*/ ) {
        console.log(to);
        if (to && to.name == "home") {
            console.log("Home -> hash", to.hash);
            let homeSection = document.getElementById(to.hash.substring(1))
            if (homeSection)
                homeSection.scrollIntoView(true);
        } else {
            window.scrollTo(0, 0);
        }
        setTimeout(() => {
            let button = document.getElementById('navbar-toggler');
            if (button.getAttribute('aria-expanded') == "true") button.click();
        }, 50);
    }
})

export default router