<template>
  <select :name="name" v-model="model" class="form-select">
    <option
      v-for="option in options_fixed"
      :key="option.value"
      :value="option.value"
    >{{option.text}}</option>
  </select>
</template>

<script>
export default {
  name: "selectComponent",
  components: {},
  data() {
    return {};
  },
  props: {
    options: {
      type: Array,
      required: true
    },
    value: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: false
    }
  },
  computed: {
    options_fixed() {
      if (typeof this.options[0] == "string") {
        return this.options.map(option => {
          return {
            value: option,
            text: option
          };
        });
      }
      return this.options;
    },
    model: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      }
    }
  },
  watch: {},
  created() {},
  methods: {
    onInput(event) {
      this.$emit("input", event.target.value);
    }
  }
};
</script>

